.app-header {
  padding-top: 70px;
  height: 50px;
  align-items: center;
  font-size: 18px;
  gap: 80px;
  @media (max-width: 600px) {
    justify-content: space-between;
  }
  .logo-svg {
    height: 40px;
    width: 160px;
  }
  nav {
    visibility: hidden;
    @media (min-width: 600px) {
      visibility: visible;
    }
    gap: 40px;
    flex-grow: 15;
    a {
      color: #fff;
      &:hover {
        color: $clr3;
      }
    }
  }
  .contact-btn {
    display: none;
    @media (min-width: 600px) {
      display: block;
    }
    background-color: inherit;
    border: 2px solid #fff;
    width: 153px;
    height: 48px;
    border-radius: 24px;
    font-family: livvic-semi-bold;
    line-height: 28px;
    font-size: 18px;
    @media (max-width: 650px) {
      font-size: 15px;
    }
    color: #fff;
  }
  .menu-icon {
    @media (min-width: 600px) {
      display: none;
    }
  }
}
