.flex {
  display: flex;
  &.space-between {
    justify-content: space-between;
  }
  &.column {
    flex-direction: column;
  }
  &.align-center {
    align-items: center;
  }
}

.clean-list {
  list-style: none;
  list-style-type: none;
}

.clean-link {
  text-decoration: none;
  color: black;
}
