.mange-build {
  background-color: $clr5;
  height: 684px;
  color: #fff;
  font-family: livvic-semi-bold;
  position: relative;
  .svg-shape {
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translateX(50%);
  }
  .main-content {
    height: 100%;
    align-items: center;
    .shape {
      background-color: $clr3;
      width: 50px;
      height: 4px;
    }
    h1 {
      width: 445px;
      height: 145px;
      font-size: 48px;
      font-weight: 700;
      line-height: 48px;
      text-align: left;
    }
    .mange-build-side {
      gap: 35px;
      .side-content {
        gap: 23px;
      }
    }
    .side-item-text {
      h1 {
        margin: 0;
        color: $clr3;
        font-family: livvic-bold;
        width: 445px;
        height: 28px;
        font-size: 18px;
        font-weight: 700;
        line-height: 28px;
        text-align: left;
      }
      p {
        margin: 0;
        /* Style for "Our networ" */
        width: 445px;
        height: 47px;
        opacity: 0.8;
        color: #ffffff;
        font-size: 15px;
        font-weight: 600;
        line-height: 25px;
        text-align: left;
      }
    }
  }
}
