.app-footer {
  height: 196px;
  background-color: #002529;
  color: #fff;
  .main-content {
    align-items: center;
    height: 100%;
    padding: 40px 165px;
    .nav-footer {
      display: flex;
      flex-direction: column;
      gap: 25px;
      img {
        width: 160px;
        height: 40px;
      }
      nav {
        gap: 40px;
        a {
          width: 49px;
          height: 28px;
          font-size: 18px;
          font-weight: 600;
          line-height: 28px;
          text-align: left;
          color: #fff;
        }
      }
    }
    .adress {
      width: 445px;
      height: 94px;
      opacity: 0.6;
      color: #ffffff;
      font-size: 15px;
      font-weight: 600;
      line-height: 25px;
      text-align: left;
    }
    .copyright-social {
      justify-content: space-between;
      height: 100%;
      .social {
        align-self: flex-end;
        gap: 16px;
        h1 {
          margin: 0;
          width: 255px;
          height: 25px;
          opacity: 0.6;
          font-size: 15px;
          font-weight: 600;
          line-height: 25px;
          text-align: right;
        }
        .social-icon {
          width: 24px;
          height: 24px;
        }
      }
    }
  }
}
