.stories {
  height: 745px;
  background-color: $clr1;
  color: #ffffff;
  .main-content {
    padding-top: 140px;
    align-items: center;
    .stories-main-text {
      margin: 0;
      width: 932px;
      height: 100px;
      font-family: livvic-bold;
      font-size: 48px;
      font-weight: 700;
      line-height: 48px;
      text-align: center;
      span {
        color: $clr4;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
      }
    }
    .stories-items {
      margin-top: 90px;
      .story-item {
        position: relative;
        align-items: center;
        gap: 30px;
        .quotes-svg{
            position: absolute;
            z-index: 1;
            width: 67px;
            height: 56px;
        }
        .quotes-text {
            padding-top: 20px;
          width: 350px;
          height: 96px;
          font-size: 15px;
          font-weight: 600;
          line-height: 25px;
          text-align: center;
          z-index: 2;
        }
        .person-info {
          h1 {
            margin: 0;
            width: 350px;
            height: 28px;
            color: $clr4;
            font-family: livvic-bold;
            font-size: 18px;
            font-weight: 700;
            line-height: 28px;
            text-align: center;
          }
          p {
            margin: 0;
            width: 350px;
            height: 18px;
            font-size: 13px;
            font-weight: 500;
            line-height: 18px;
            text-align: center;
            font-style: italic;
          }
        }
        .profile-img {
          height: 62px;
          width: 62px;
          border-radius: 50%;
          border: 2px solid #c4fffe;
        }
      }
    }
  }
}
