.home-start {
  height: 200px;
  background-color: $clr3;
  position: relative;
  .svg-shape {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 200px;
  }
  .main-content {
    height: 100%;
    align-items: center;
    padding: 0 260px;
    h1 {
      margin: 0;
      width: 504px;
      height: 53px;
      color: $clr5;
      font-family: livvic-bold;
      font-size: 48px;
      font-weight: 700;
      line-height: 48px;
      text-align: left;
    }
    button {
      width: 153px;
      height: 48px;
      border-radius: 24px;
      border: 2px solid $clr5;
      background-color: inherit;
      color: $clr5;
      font-size: 18px;
      font-weight: 600;
      line-height: 28px;
      text-align: center;
    }
  }
}
