.home-hero {
  background-color: $clr1;
  height: 567px;
  @media (min-width: 600px) {
    height: 700px;
  }
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .main-hero-content {
    @media (max-width: 1200px) {
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 20px;
    }

    .hero-text {
      margin: 0;
      width: 327px;
      height: 81px;
      color: #ffffff;
      font-family: livvic-bold;
      font-size: 40px;
      font-weight: 700;
      line-height: 40px;
      text-align: center;
      @media (min-width: 600px) {
        font-size: 64px;
        line-height: 56px;
        height: 121px;
        text-align: center;
      }
      @media (min-width: 1200px) {
        font-size: 100px;
        width: 635px;
        height: 201px;
        line-height: 100px;
        text-align: left;
      }
      span {
        color: $clr3;
      }
    }
    .hero-sub-text {
      .shape {
        @media (max-width: 1200px) {
          display: none;
        }
        height: 4px;
        width: 50px;
        background-color: $clr4;
      }
      p {
        margin: 0;
        width: 327px;
        height: 108px;
        color: #ffffff;
        font-family: livvic-semi-bold;
        font-size: 15px;
        font-weight: 600;
        line-height: 28px;
        text-align: center;
        @media (min-width: 600px) {
          width: 457px;
          height: 80px;
        }
        @media (min-width: 1200px) {
          width: 445px;
          height: 109px;
          font-size: 18px;
          line-height: 28px;
          letter-spacing: normal;
          text-align: left;
        }
      }
    }
  }
  .hero-svgs {
    display: flex;
    justify-content: flex-end;
    @media (max-width: 600px) {
      width: 100%;
      img {
        width: 100%;
        height: 115px;
      }
    }
    @media (max-width: 1200px) {
      justify-content: center;
    }
  }
}
